/* Card styles */
.rcard {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 25px;
  margin-bottom: 25px;
  position: relative;
  width: 300px;
  -webkit-box-shadow: 0 3px 6px #999, 0 3px 6px #999;
          box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  text-align: left;
}

.rcard > .img-container {
height: 100%;
overflow: hidden;
text-align: center;
background:#6CADDC;
margin-bottom: 10px;
}
.rcard > .img-container > img {
width: 100%;
height: 100%;
}

.rcard > .content {
text-align: center;
}

/* Modal Animations */
.transition-enter {
-webkit-transform: scale(0);
    -ms-transform: scale(0);
        transform: scale(0);
}
.transition-enter-active {
-webkit-transform: scale(1);
    -ms-transform: scale(1);
        transform: scale(1);
-webkit-transition: -webkit-transform 1000ms ease;
transition: -webkit-transform 1000ms ease;
-o-transition: transform 1000ms ease;
transition: transform 1000ms ease;
transition: transform 1000ms ease, -webkit-transform 1000ms ease;
}
.transition-exit {
-webkit-transform: scale(1);
    -ms-transform: scale(1);
        transform: scale(1);
}
.transition-exit-active {
-webkit-transform: scale(0);
    -ms-transform: scale(0);
        transform: scale(0);
-webkit-transition: -webkit-transform 1000ms ease;
transition: -webkit-transform 1000ms ease;
-o-transition: transform 1000ms ease;
transition: transform 1000ms ease;
transition: transform 1000ms ease, -webkit-transform 1000ms ease;
}



